<template>
    <div>


            <hooper ref="carousel" class="carousel-thumbs carousel-hooper" :class="{'carousel-thumbs--video':type === 'video'}" :wheelControl="false" :itemsToShow="3" :centerMode="false" :trimWhiteSpace="true" >
                <slide :key="index"
                       v-for="(item,index) in items">
                    <div v-if="type === 'photo'"
                         class="item">
                        <a @click="showViewer(index+1)"
                           href="javascript:void(0)"
                           v-if="index == 0"
                           :style="{'background-image': 'url(http://localhost:8080/img/img1.a3de5dd0.jpg)'}"></a>
                        <a @click="showViewer(index+1)"
                            href="javascript:void(0)"
                            v-if="index == 1"
                            :style="{'background-image': 'url(http://localhost:8080/img/img2.44371d45.jpg)'}"></a>
                        <a @click="showViewer(index+1)"
                           href="javascript:void(0)"
                           v-if="index == 2"
                           :style="{'background-image': 'url(http://localhost:8080/img/img3.d85da2f1.jpg)'}"></a>
                       <a @click="showViewer(index+1)"
                          href="javascript:void(0)"
                          v-if="index == 3"
                          :style="{'background-image': 'url(http://localhost:8080/img/img4.4433c90d.jpg)'}"></a>
                    </div>
                </slide>

                <hooper-navigation slot="hooper-addons"></hooper-navigation>
            </hooper>


        <div class="viewer-container viewer-backdrop viewer-fixed viewer-hooper viewer-fade" v-if="type === 'photo' || type === 'photo-coupons' && viewerShow" ref="viewer">

          <div class="viewer-canvas-wrapper" v-clickoutside="closeViewer">
            <div class="viewer-canvas">
              <hooper class="carousel-thumbs viewer-carousel" ref="viewerHooper" @slide="checkVideoViewer">
                <slide :key="index"
                       v-for="(item,index) in items">
                    <div class="item" v-if="index == 0">
                        <img src="http://localhost:8080/img/img1.a3de5dd0.jpg" alt="">
                    </div>
                    <div class="item" v-if="index == 1">
                        <img src="http://localhost:8080/img/img2.44371d45.jpg" alt="">
                    </div>
                    <div class="item" v-if="index == 2">
                        <img src="http://localhost:8080/img/img3.d85da2f1.jpg" alt="">
                    </div>
                    <div class="item" v-if="index == 3">
                        <img src="http://localhost:8080/img/img4.4433c90d.jpg" alt="">
                    </div>
                </slide>

                <hooper-navigation slot="hooper-addons"></hooper-navigation>
              </hooper>
            </div>
            <div role="button" class="viewer-button viewer-close" @click="closeViewer()"></div>
          </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import Popup from '@/components/modal/Popup.vue';
    import { Hooper, Slide, Navigation as HooperNavigation } from 'hooper'; // carousel


    export default {
        name: "SupplierGallery",
        components: {
            Hooper,
            Slide,
            HooperNavigation,
            Popup
        },
        props: {
            type: {
                default: 'photo', // video
            },
            items: {
                default(){
                    return [];
                }
            },
            firstphoto:{
                default(){
                    return false;
                }
            },
            showFirstPhoto:{
                default(){
                    return false;
                }
            },
        },
        data(){
            return {
                currentId: false,
                modal: false,
                viewerShow: false,
                unsubscribe: '',
                testItems:
                [{"id":"28206","code":1,"name":"IMG_6360.JPG","size":"1076174","path":"/upload/resize_cache/skidki/provider/5b4//1024_768_1/IMG_6360.JPG","icon":"/upload/resize_cache/skidki/provider/5b4//160_160_1/IMG_6360.JPG","finish":true,"ext":"JPG","progress":100, "mediaType": 'photo'},
                {"id": "29314", "mediaType": "video", "embedId": "WAFkkviz92Q"},
                {"id": "29351", "mediaType": "video", "embedId": "ZIVL4q3DUgs"},
                {"id":"28208","code":2,"name":"17.AKN_5540_a диван.jpg","size":"50318","path":"/upload/skidki/provider/eff/17.AKN_5540_a-divan.jpg","icon":"/upload/resize_cache/skidki/provider/eff//160_160_1/17.AKN_5540_a-divan.jpg","finish":true,"ext":"jpg","progress":100, "mediaType": 'photo'},
                {"id":"28209","code":3,"name":"19.HLOYA диван.jpg","size":"46602","path":"/upload/skidki/provider/e1d/19.HLOYA-divan.jpg","icon":"/upload/resize_cache/skidki/provider/e1d//160_160_1/19.HLOYA-divan.jpg","finish":true,"ext":"jpg","progress":100, "mediaType": 'photo'},
                {"id":"28210","code":4,"name":"20.KAROLIN диван.jpg","size":"47351","path":"/upload/skidki/provider/e9b/20.KAROLIN-divan.jpg","icon":"/upload/resize_cache/skidki/provider/e9b//160_160_1/20.KAROLIN-divan.jpg","finish":true,"ext":"jpg","progress":100, "mediaType": 'photo'},
                {"id":"28211","code":5,"name":"21.AMELI диван.jpg","size":"68886","path":"/upload/skidki/provider/d62/21.AMELI-divan.jpg","icon":"/upload/resize_cache/skidki/provider/d62//160_160_1/21.AMELI-divan.jpg","finish":true,"ext":"jpg","progress":100, "mediaType": 'photo'},],
                showFullscreen: true,
                currentSlide: '',
            }
        },
        watch: {
            showFirstPhoto: function () {
                this.showViewer(0);
            }
        },
        beforeMount(){
            this.$nextTick(function () {
                this.viewerShow = true;
            });
        },
        mounted: function () {
            this.unsubscribe = this.$store.subscribe((mutation, state) => {
                if(mutation.type=='setTabClick'){
                        this.updateCarousel();
                }
            });
        },
        beforeDestroy: function(){
            this.unsubscribe();
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
            countItems() {
                return this.items.length;
            },
            colItems() {
                let col = 6;
                switch (this.countItems) {
                    case 1:
                        col = 24;
                        break;
                    case 2:
                        col = 12;
                        break;
                    case 3:
                        col = 8;
                        break;
                }
                return col;
            },
            viewerImages(){
                let images = [];

                /*if(this.firstphoto){
                    images.push('//api.zemskidki.ru/upload/resize_cache/skidki/provider/f28/705_650_1/rimskie_shtory_s_elektroprivodom_3.jpg')
                }*/
                for(let item in this.items){
                    images.push('//api.zemskidki.ru'+this.items[item].path)
                }
                return images;
            }
        },
        methods: {
            checkVideoViewer(payload){
              let currentSlide = payload.currentSlide;
              if(this.testItems[currentSlide].mediaType != 'video'){
                this.pauseVideos();
              }
            },
            checkVideo(payload){
              this.currentSlide = payload.currentSlide;
              if(this.testItems[this.currentSlide].mediaType != 'video'){
                this.pauseVideos();
                this.showFullscreen = true;
              }else{
                this.showFullscreen = false;
              }
            },
            close() {
                this.modal = false;
            },
            showVideo(item){
                this.currentId = item.id;
                let self = this;
                setTimeout(function(){
                  self.modal = true;
                },10);
            },
            closeViewer(){
              this.$refs.viewer.classList.add('viewer-transition');
              this.$refs.viewer.classList.remove('viewer-in');
              let self = this;
              setTimeout(function(){
                self.$refs.viewer.classList.remove('viewer-transition');
                self.$refs.viewerHooper.restart();
                self.pauseVideos();
              }, 200);
            },
            pauseVideos(){
              [...document.getElementsByTagName('iframe')].forEach(i => i.contentWindow.postMessage('{"event":"command","func":"pauseVideo","args":""}', '*'));
            },
            showViewer(index) {
                this.$refs.viewerHooper.slideTo(index);
                this.$refs.viewer.classList.add('viewer-transition');
                this.$refs.viewer.classList.add('viewer-in');
                let self = this;
                setTimeout(function(){
                  self.$refs.viewer.classList.remove('viewer-transition');
                }, 400);
                setTimeout(function(){
                  self.$refs.viewer.classList.add('viewer-in');
                },10);
            },
            updateCarousel() {
                if(this.$refs.carousel){
                    setTimeout(()=>{
                        if(this.$refs.carousel !== undefined){
                            this.$refs.carousel.updateWidth();
                        }
                    },3);
                }
            }
        },
        directives: {
            'clickoutside': {
                bind: function(el, binding, vNode) {
                    if (typeof binding.value !== 'function') {
                        const compName = vNode.context.name;
                        let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                        if (compName) { warn += `Found in component '${compName}'` }
                        //console.warn(warn);
                    }
                    const bubble = binding.modifiers.bubble;
                    const handler = (e) => {
                        if (bubble || (!el.contains(e.target) && el !== e.target) && (!e.target.classList.contains('click-outside-tooltip'))) {
                            binding.value(e)
                        }
                    }
                    el.__vueClickOutside__ = handler;
                    document.addEventListener('click', handler);
                },

                unbind: function(el, binding) {
                    document.removeEventListener('click', el.__vueClickOutside__);
                    el.__vueClickOutside__ = null;
                }
            }
        }
    }
</script>

