<script>
    import {mapState} from 'vuex';
    import InputStyled from '@/components/forms/InputStyled.vue';
    import { Carousel3d, Slide } from 'vue-carousel-3d';
    import SupplierGallery from '@/components/SupplierGallery.vue';
    import {scrollTo} from '@/helpers/animate.js';
    export default {
        metaInfo() {
            return {
                title: 'Поставщик',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "NewSupplier",
        components: {
          InputStyled,
          Carousel3d,
          Slide,
          SupplierGallery,
        },
        data: function () {
            return {
              color: '2F80ED',
              currentColor: '2F80ED',
              currentActiveIndex: 0,
              headerFixed: false,
              slidedown: false,
              tabs: [{id: 0, name: 'О компании', href: "#tab-about"},
                     {id: 1, name: 'Преимущества', href: "#tab-advantages"},
                     {id: 2, name: 'Портфолио', href: "#tab-portfolio"},
                     {id: 3, name: 'Оборудование', href: "#tab-instruments"},
                     {id: 4, name: 'Магазины', href: "#tab-shops"},
                     {id: 5, name: 'Офисы', href: "#tab-offices"},
                     {id: 6, name: 'Производство', href: "#tab-production"},
                     {id: 7, name: 'Коллектив', href: "#tab-staff"}],
            }
        },
        mounted() {
            //window.addEventListener('scroll', this.handleScroll);
            ym(39511350, 'hit', this.$route.path, {title: 'Поставщик | ZemSkidki.ru'});

            window.addEventListener('scroll', this.handleScroll);
        },
        beforeDestroy(){
          window.removeEventListener('scroll', this.handleScroll);
        },
        computed: {
            ...mapState({
                currentCity:  state => state.cities.current,
                statistic:  state => state.rating.statistic,
                providers:  state => state.rating.providers,
                sort:  state => state.rating.sort.current,
                action: state => state.actions.detail,
            }),
        },
        methods: {
          setCurrentColor(){
            this.currentColor = this.color;
          },
          hexToRgb(hex) {
            var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
            return result
              ? `${parseInt(result[1], 16)}, ${parseInt(result[2], 16)}, ${parseInt(result[3], 16)}`
              : "0, 0, 0";
          },
          setActiveTab(index, href){
            this.currentActiveIndex = index;
            scrollTo(href, true, 20);
          },
          handleScroll: function () {
            let self = this;
            if(window.pageYOffset > 350){
              this.headerFixed = true;
              setTimeout(function(){
                self.slidedown = true
              },10);
            }else if(window.pageYOffset < 350 && window.pageYOffset > 300){
              this.slidedown = false;
            }else{
              this.headerFixed = false;
            }
            for(let i = 0; i < this.tabs.length; i++){
              let elem = document.querySelector(this.tabs[i].href);
              let top = elem.getBoundingClientRect().top;
              if(top < 130 ){
                this.currentActiveIndex = i;
              }
            }

          },
      }
    }
</script>
